@mixin Desktop {
  @media(min-width: 990px) {
    @content
  }
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide img {
  display: block;
  width: 100%;
  object-fit: cover;
}

.home-guesthouse-swiper img {
  height: 200px;
}

.details-swiper img {
  height: 260px;
  @include Desktop() {
    height: 480px;
  }
}

.swiper-button-next,
.swiper-button-prev {
  color: white;
  z-index: 10;
  top: 22px;
  width: calc(var(--swiper-navigation-size)/ 35 * 27);
  height: 100%;
  background-color: rgba(255, 255, 255, 0.5);
}

.swiper-button-next:hover,
.swiper-button-prev:hover {
  color: #5C9500;
}

.swiper-button-next{
  right: 0;
}
.swiper-button-prev{
  left: 0;
}

.swiper-pagination-bullet-active {
  background: #5C9500;
}